import React, {useState} from 'react'
import { Link, withRouter, Redirect } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow,
  CAlert
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import ArvamLogo from '../../../assets/ArvamLogo.png'
import api from '../../../config';
import axios from 'axios';
const ForgotPassword = ({history}) => {

  const [values, setValues] = useState({
    email: '',
  });
  const [error, setError] = useState(false);
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    e.preventDefault();
    setValues({...values,
      [e.target.name] : e.target.value
    })
    console.log(values);

  }
  const handleSubmit = (event) => {
    event.preventDefault();
    const config = {
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Accept' : '*/*',
        'host': 'arbam-test.herokuapp.com',
        'Content-Type':'application/x-www-form-urlencoded',
      }
    }
    var formData = new FormData();
    formData.append('email', values.email);
    setLoading(true);
    axios.post(api.server+'/api/admin/notifications/emails',formData, config)
    .then(async function (response) {
      console.log(response);
      // const email = response.data.data.email;
      if(response.status === 200){
        await localStorage.removeItem('mail');
        await localStorage.setItem('mail', values.email);
        history.push('/validate-code',{email: values.email});
      } else {
        setError(true);
        console.log('ha ocurrido un error',response.status)
        await setMessage('El Email ingresado no tiene un formato valido')
        await localStorage.removeItem('mail');
      }
    })
    .catch(function (error) {
      console.log(error);
      setLoading(false);
      setError(true);
      setTimeout(() => {
        setError(false)
      }, 6000);
    });
  }
  const handleSignIn = event => {
    event.preventDefault();
    var formData = new FormData();
    formData.append('email', values.email);
    formData.append('password', values.password);
    setLoading(true);
    fetch(api.server+'/api/admin/login',{
      method:'POST',
      headers:{
        'Accept' : '*/*',
        'Content-Type':'application/x-www-form-urlencoded',
      },
      body: formData
    })
      .then((response)=>response.json())
      .then((responseJson)=>{
        if(responseJson.status === 0){
          // console.log('error');
          setLoading(false);
          // setShowMsg(true);
          // setMsgUser('Usuario o password son invalidos,Intente nuevamente');

        }else{
          //data user
          const id    = responseJson.data.id;
          const email      = responseJson.data.email;
          const token = responseJson.token;

          //created sessionStorage
          sessionStorage.setItem('iduser',id);
          sessionStorage.setItem('email',email);
          sessionStorage.setItem('apitoken',token);
          history.push('/');
        }
      })
      .catch((error)=>{
        console.log(error);
        history.push('/');

      })
  }

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h2>Recuperar contraseña</h2>
                    <p className="text-muted">Te enviaremos el codigo de recuperacion a tu Email</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput valid={values.email.includes('@') && !error} invalid={(!values.email.includes('@') && values.email!=='') || error} id="email" onChange={(e) => handleChange(e)} name="email" type="text" placeholder="Ingresa tu Email" autoComplete="username" />
                    </CInputGroup>
                    { error ? <CCol>
                    <CInputGroup>
                        <CAlert color="danger">
                         {'El Email ingresado no tiene un formato valido'}
                        </CAlert>
                    </CInputGroup>
                </CCol>: null}
                    <CRow>
                      <CCol xs="6">
                        <CButton color="primary" onClick={handleSubmit} className="px-4">Enviar Código</CButton>
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
              <CCard className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                <CCardBody className="text-center">
                  <img style={{ width: 120, height: 159}} src={ArvamLogo} />
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default withRouter(ForgotPassword)
